@import url('https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap');

:root {
    --offWhite: #EEF4ED;
    --lightBlue: #8DA9C4;
    --mediumBlue: #1E5BA1;
    --darkBlue: #13315C;
    --darkestBlue: #0B2545;
}

#root {
    background-color: #FDFEFD;
}


/*
==============================
HEADER / TEXT STYLING
==============================
*/
h1 {
    font-family: Ubuntu;
    font-size: 88px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 30.72px;
    text-transform: uppercase;

    margin: 0 0 20px 0;
}

h2 {
    font-family: Ubuntu;
    font-size: 72px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 23.04px;
    text-transform: uppercase;

    margin: 0 0 20px 0;
}

h3 {
    font-family: Ubuntu;
    font-size: 60px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 19.2px;
    text-transform: uppercase;

    margin: 0 0 20px 0;
}

h4 {
    font-family: Ubuntu;
    font-size: 30px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 9.6px;

    margin: 0 0 20px 0;
}

h5 {
    font-family: Ubuntu;
    font-size: 40px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    text-transform: uppercase;
    margin: 0;
}

h6 {
    font-family: Ubuntu;
    font-size: 26px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    text-transform: uppercase;

    margin: 0 0 20px 0;
}

p {
    font-family: Ubuntu;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%; /* 30.8px */

    margin: 0 0 10px 0;
}

p.nav {
    font-family: Ubuntu;
    font-size: 23px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    text-transform: uppercase;

    margin: 0;
}

button {
    font-family: Ubuntu;
    font-size: 20px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    text-transform: uppercase;

    background-color: transparent;
    border: 2px solid var(--offWhite);
    padding: 10px 10px;
    border-radius: 20px;
}

/*
==============================
GLOBAL STUFF
==============================
*/
.horizontal-line {
    width: 3px;
    background-color: var(--lightBlue);
    flex-shrink: 0;
}

.info {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 200px 60px;
}

.content {
    flex-grow: 1;
    padding: 100px 0px 100px 60px;
    
    display: flex;
    flex-direction: column;
    gap: 60px;

    
}

section {
    padding: 0 60px;
    
    display: flex;
    gap: 30px;

    &:last-of-type {
        .content {
            padding: 100px 0px 200px 60px;
        }
    }

    &:first-of-type {
        .content {
            padding: 200px 0px 100px 60px;
        }
    }
}

.multiple-sib {
    display: flex;
    justify-content: space-between;
    gap: 66px;

    & > .small-icon-block {
        flex: 1;
    }

    @media all and (max-width: 800px) {
        flex-direction: column;
    }
}

.multiple-hwt {
    display: flex;
    justify-content: space-between;
    gap: 40px;
}

.multiple-tsb {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 60px;

    .tech-stack-block {
        flex: 1 1 40%;
    }
}

.block-and-text {
    display: flex;
    align-items: center;
    gap: 64px;

    .large-icon-block {
        flex: 1 0 423px;
    }

    .text {
        flex: 1 1 60%;
        .status {
            display: flex;
            align-items: center;
            gap: 20px;

            margin-bottom: 15px;
        }
    }
}

