/*
==============================
NAVIGATION
==============================
*/
.menu {
    transition: all ease-in-out 300ms;
}
nav {
    padding: 20px 40px;
    border-radius: 0px 0px 30px 30px;
    background-color: var(--mediumBlue);
    border: 3px solid var(--offWhite);
    transition: all ease-in-out 300ms;

    position: relative;
    margin-bottom: -30px;

    .nav-menu {
        display: flex;
        justify-content: space-between;
        gap: 20px;
    }

    a {
        display: flex;
        /* flex-direction: column; */
        align-items: center;
        gap: 10px;

        color: var(--offWhite);
        text-decoration: none;
        font-size: 20px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        text-transform: uppercase;

        padding: 10px;
        border: 2px solid transparent;
        border-radius: 10px;
        transition: all ease-in-out 300ms;

        svg {
            width: 30px;
            height: 30px;
            flex-shrink: 0;
            align-self: center;
        }

        &:hover {
            /* background-color: var(--lightBlue); */
            border: 2px solid var(--offWhite);
            cursor: pointer;
        }
    }
}

/*
==============================
FOOTER 
==============================
*/

footer {
    padding: 30px 40px;
    border-radius: 30px 30px 0px 0px;
    background-color: var(--mediumBlue);
    border: 3px solid var(--offWhite);
    transition: all ease-in-out 300ms;

    position: relative;
    margin-top: -30px;
    /* min-height: 96px; */

    display: flex;
    justify-content: space-between;
    gap: 40px;

    .name {
        color: var(--offWhite);
        flex: 1;

        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 10px;
    }

    .line {
        background-color: var(--offWhite);
        width: 3px;
    }
}

